import styles from './TextResult.module.css'

const textResults = [
   "–í–æ–∑–º–æ–∂–Ω–æ, –í—ã —á–µ–≥–æ-—Ç–æ –Ω–µ –∑–Ω–∞–µ—Ç–µ –∏–ª–∏ –≤ —á—ë–º-—Ç–æ –Ω–µ —É–≤–µ—Ä–µ–Ω—ã, –Ω–æ —É –í–∞—Å —è–≤–Ω–æ –µ—Å—Ç—å —Ö–æ—Ä–æ—à–∏–µ —à–∞–Ω—Å—ã —É—Å–ø–µ—à–Ω–æ –ø—Ä–æ–π—Ç–∏ –æ–±—É—á–µ–Ω–∏–µ –∏ —Å—Ç–∞—Ç—å –∫–ª–∞—Å—Å–Ω—ã–º —Å–ø–µ—Ü–∏–∞–ª–∏—Å—Ç–æ–º!",
   "–ö–∞–∂–µ—Ç—Å—è, –í—ã –∏–Ω—Ç–µ—Ä–µ—Å—É–µ—Ç–µ—Å—å —Å—Ñ–µ—Ä–æ–π –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–æ–Ω–Ω—ã—Ö —Ç–µ—Ö–Ω–æ–ª–æ–≥–∏–π –∏–ª–∏ –¥–∞–∂–µ –∏–º–µ–µ—Ç–µ –Ω–µ–∫–æ—Ç–æ—Ä—ã–π –æ–ø—ã—Ç. –í —Ç–∞–∫–æ–º —Å–ª—É—á–∞–µ, —Å–ø–µ—Ü–∏–∞–ª—å–Ω–æ—Å—Ç—å \"–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏–æ–Ω–Ω—ã–µ —Å–∏—Å—Ç–µ–º—ã –∏ –ø—Ä–æ–≥—Ä–∞–º–º–∏—Ä–æ–≤–∞–Ω–∏–µ\" –æ—Ç–ª–∏—á–Ω–æ –í–∞–º –ø–æ–¥–æ–π–¥—ë—Ç!",
   "–í—ã —É–≤–µ—Ä–µ–Ω–Ω–æ —á—É–≤—Å—Ç–≤—É–µ—Ç–µ —Å–µ–±—è –≤ —Å—Ñ–µ—Ä–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–æ–Ω–Ω—ã—Ö —Ç–µ—Ö–Ω–æ–ª–æ–≥–∏–π –∏, –≤–æ–∑–º–æ–∂–Ω–æ, —É–∂–µ –∏–º–µ–µ—Ç–µ —Ö–æ—Ä–æ—à–∏–µ –∑–Ω–∞–Ω–∏—è –∏ –Ω–∞–≤—ã–∫–∏ –≤ —ç—Ç–æ–π –æ–±–ª–∞—Å—Ç–∏. –¢–æ–≥–¥–∞ –≤–∞–º —Å—Ç–æ–∏—Ç –ø—Ä–æ–π—Ç–∏ –æ–±—É—á–µ–Ω–∏–µ –≤ –Ω–∞—à–µ–º —É–Ω–∏–≤–µ—Ä—Å–∏—Ç–µ—Ç–µ –ø–æ —Å–ø–µ—Ü–∏–∞–ª—å–Ω–æ—Ç–∏ \"–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏–æ–Ω–Ω—ã–µ —Å–∏—Å—Ç–µ–º—ã –∏ –ø—Ä–æ–≥—Ä–∞–º–º–∏—Ä–æ–≤–∞–Ω–∏–µ\"! –í—ã –Ω–∞–π–¥—ë—Ç–µ –º–Ω–æ–≥–æ –µ–¥–∏–Ω–æ–º—ã—à–ª–µ–Ω–Ω–∏–∫–æ–≤ –∏ —Å–º–æ–∂–µ—Ç–µ –ø—Ä–æ–∫–∞—á–∞—Ç—å —Å–≤–æ–∏ –Ω–∞–≤—ã–∫–∏.",
];

const TextResult = ({percentResult}) => {

   let text;

   if (percentResult <= 35) {
      text = textResults[0];
   } else if (percentResult <= 75) {
      text = textResults[1];
   } else {
      text = textResults[2];
   }

  return (
      <p className={styles.resultText}>
         ü•≥ {text}
      </p>
  )
}

export default TextResult
